.product__pageProduct{
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: center;
    line-height: 30px;
    font-size: 18px;
    margin: auto;
    padding-top: 10px;
}
.product__productImageURL{
    max-width: 60vh;
    max-height: 70vh;
    object-fit: contain;
    padding-right: 10px;
}
@media only screen and (max-width: 600px){
    .product__pageProduct{
        display: block;
        width: 90%;
        font-size: 25px;
    }
}